import React from "react";
import { graphql } from "gatsby";
import Layout from "../Components/Layout";
import { SEO } from "theme-seo";

const Post = ({ data, pageContext }) => {
  const post = data.markdownRemark;
  const imageUrl = data.allFile.nodes.filter((n) => n.name === post.frontmatter.icon)?.[0]
    .publicURL;
  return (
    <Layout>
      <SEO
        title={pageContext.title}
        description={post.frontmatter.description}
        imageUrl={imageUrl}
      />
      <main className="text-right w-10/12 lg:w-3/5 mx-auto">
        <div className="WordpressStyles markdownBlog">
          <div className="lg:py-2">
            <h1
              className="lg:p-4 text-gray-600"
              dangerouslySetInnerHTML={{ __html: post.frontmatter.title }}
            />
            <div
              className="text-gray-600 lg:px-8"
              dangerouslySetInnerHTML={{ __html: post.html }}
            />
          </div>
        </div>
      </main>
    </Layout>
  );
};

export default Post;

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      html
      frontmatter {
        title
        description
        keywords
        icon
      }
    }

    allFile(filter: { sourceInstanceName: { eq: "post" }, extension: { ne: "md" } }) {
      nodes {
        name
        publicURL
        extension
      }
    }
  }
`;
